import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { request } from '../helper';

class FixedDepositTransaction extends React.Component {
  constructor (props) {
    super(props);
    this.state = { value: '' };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  handleSubmit(event) {
    alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
  }

  click = async () => {
    if (this.state.value == '') {
      alert("Please enter Aadhaar Number or Mobile Number of a Customer");
    }
    let res = await request('customer/search').post({ query: this.state.value });
    let status = await res.json();
    console.log(status);
    if (status.status === 200) {
      this.props.history.push({
        pathname: `/Home/FixedDeposit`,
        state: status.message
      });
    }
  }

  render() {
    return (
      <Fragment>

        <section className="mx-auto col-sm-8 p-3 m-0 rounded">

          <div className="headTwo">
            <h4 className="text-center mx-auto col-sm-11 mb-3 p-2" style={{ color: "blue" }}>Fixed Deposit Transactions</h4>
            <div className="d-flex justify-content-between form-group pt-0 pb-0 pr-3 pl-3" >
              <input type="text" className="col-sm-10 p-0 form-control p-2"
                value={this.state.value} onChange={this.handleChange}
                placeholder="Search by Mobile Number or Aadhaar Number" />
              <button type="submit" className="btn btn-primary d-block btn-sm mx-auto"
                style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
                onClick={() => this.click()}>Search</button>
            </div>
          </div>
        </section>

      </Fragment>
    );
  }
}

export default withRouter(FixedDepositTransaction);