import React, { Fragment, useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
//import { request } from '../helper';
import { withRouter, useParams } from 'react-router-dom';

const Example = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  const [data, setData] = useState('');

  useEffect(() => {
    if (props.location.state === undefined) {
      props.history.push('/Home/addcustomer');
      return;
    }

    setData(props.location.state);
  }, []);

  let selectedDate = (date) => {
    setStartDate(date);
  }

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Customer</h4>
        <div className="headTwo pt-4">

          <form onSubmit={props.handleSubmit}>
            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Title</label>
                <div>
                  <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['title']} defaultValue={data['title']} />
                </div>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" style={{ display: "block" }}>
                <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['firstname']} defaultValue={data['firstname']} autoComplete="off" />
              </div>

              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['lastname']} defaultValue={data['lastname']} autoComplete="off" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Mobile Number</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['mobile']} defaultValue={data['mobile']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['email']} defaultValue={data['email']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
                <DatePicker className="form-control" selected={startDate} readOnly onChange={date => selectedDate(date)} />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Pan Card</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['pan']} defaultValue={data['pan']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Aadhaar Number</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder={data['aadhaar']} readOnly defaultValue={data['aadhaar']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Location</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder={data['location']} readOnly defaultValue={data['location']} autoComplete="off" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Branch Name</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['branch']} defaultValue={data['branch']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">

              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">

              </div>
            </div>

            <div className="d-flex justify-content-center"><h5>Present Address</h5></div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['present_line1']} defaultValue={data['present_line1']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder={data['present_line2']} readOnly defaultValue={data['present_line2']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder={data['present_city']} readOnly defaultValue={data['present_city']} autoComplete="off" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['present_district']} defaultValue={data['present_district']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder={data['present_state']} readOnly defaultValue={data['present_state']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder={data['present_state']} readOnly defaultValue={data['present_state']} autoComplete="off" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['present_pincode']} defaultValue={data['present_pincode']} autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            </div>

            <div className="d-flex justify-content-center"><h5>Permanent Address</h5></div>
            <div>
              <input type="checkbox" id="check" name="check" value="Boat" autoComplete="off" />
              <label htmlFor="check"> Same as present address please click it</label>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Line 1" autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Line 2" autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder="City Name" autoComplete="off" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="District" autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder="State" autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Country" autoComplete="off" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Pin Code" autoComplete="off" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            </div>

            {/* <div className="d-flex" style={{ width: "100%" }}>
              <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}>Submit</button>
            </div> */}
          </form>

        </div>

      </section>
    </Fragment>
  );
}
export default withRouter(Example);