import React, { Fragment, useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import { withRouter } from 'react-router-dom';
import DatePicker from "react-datepicker";
import { request } from '../helper';
import "react-datepicker/dist/react-datepicker.css";

const Example = (props) => {
  const [policyDate, setPolicyDate] = useState(new Date());
  const [dateOfEntry, setDateOfEntry] = useState(new Date());
  const [chequeDatetype, setChequeDateType] = useState(new Date());
  const [data, setData] = useState('');
  const [gross, setGross] = useState('');
  const [revenue, setRevenue] = useState(0);
  const [netAmount, setNetAmount] = useState('');
  const [companies, setCompany] = useState([]);
  const [plans, setPlans] = useState('');
  const [ppt, setPPT] = useState([]);
  const [mode, setMode] = useState('');
  const [companyPlans, setCompanyPlans] = useState([]);
  const [selectedCompany, setSelectedCompany] = useState('');
  const [selectedPPT, setSelectedPPT] = useState('');
  const [selectedStartDate, setSelectedStartDate] = useState('');
  const [selectedPolicy, setSelectedPolicy] = useState('');
  const [stage, setStage] = useState('');
  const [businessType, setBusinessType] = useState('');

  useEffect(() => {
    if (props.location.state === undefined) {
      props.history.push('/Home/lifeinsurancetransaction');
      return;
    }

    setData(props.location.state);

    (async () => {
      let res = await request('lifeinsurancerevenue/columns').get();
      setCompany(await res.json());
    })();
  }, []);

  // let dateFormat = (date) => {
  //   let [month, day, year] = date.split("/");
  //   return `${day}-${month}-${year}`;
  // }

  let setDate = (date) => {
    setDateOfEntry(date);
    setSelectedStartDate(
      // dateFormat(
        new Date(date).toLocaleDateString()
      // )
    );

    console.log(dateOfEntry);
  }

  let setPolicy = (date) => {
    setPolicyDate(date);
    setSelectedPolicy(
      // dateFormat(
        new Date(date).toLocaleDateString()
      // )
    );
    console.log(policyDate);
  }

  let fetchPlans = async ({ target: { value } }) => {
    setSelectedCompany(value);
    let res = await request(`lifeinsurancerevenue/plans/${value}`).get();
    setCompanyPlans(await res.json());
  }

  let fetchPpt = async ({ target: { value } }) => {
    let res = await request(`lifeinsurancerevenue/ppt/${value}`).get();
    setPPT(await res.json());
  }

  let getRevenue = (amount, ppt, func) => {
    func(Number(amount) / 100 * Number(ppt));
    setSelectedPPT(ppt);
  }

  let check = () => {
    if (plans === "") {
      alert("Please Select Plan Type");
      return false;
    } else if (ppt.length === 0 || ppt === undefined || ppt === null) {
      alert("Please Select PPT");
      return false;
    } else if (companyPlans === undefined || companyPlans === null || companyPlans.length === 0) {
      alert("Please Select Plan Name");
      return false;
    } else if (selectedCompany === "" || selectedCompany === undefined || selectedCompany === null) {
      alert("Please Select Company");
      return false;
    }

    return true;
  }

  let getNetAmount = (amount, func) => {

    if (!check()) return;

    setGross(amount);

    switch (plans) {
      case "ULIPs":
        func(Number(amount) / 100 * 4.5);
        setRevenue(Number(netAmount) / 100 * Number(selectedPPT));
        break;
      case "Traditional plan":
        func(Number(amount) / 100 * 18);
        setRevenue(Number(netAmount) / 100 * Number(selectedPPT));
        break;
    }
  }

  const Mobile = React.useRef(null);
  const Adhaar = React.useRef(null);
  const RMID = React.useRef(null);
  const TSOID = React.useRef(null);
  const ApplicationNumber = React.useRef(null);
  const CompanyName = React.useRef(null);
  const PlanType = React.useRef(null);
  const PolicyTerm = React.useRef(null);
  const GrossPremium = React.useRef(null);
  const NetPremium = React.useRef(null);
  const DateofEntry = React.useRef(null);
  const PPM = React.useRef(null);
  const TypeOfBusiness = React.useRef(null);
  const ModeOfPayment = React.useRef(null);
  const PolicyNumber = React.useRef(null);
  const Stage = React.useRef(null);
  const PolicyIssueDate = React.useRef(null);
  const Revenue = React.useRef(null);
  const PaymentTerm = React.useRef(null);
  const AccountNumber = React.useRef(null);
  const ChequeNumber = React.useRef(null);
  const ChequeAccount = React.useRef(null);
  const ChequeDate = React.useRef(null);
  const BankName = React.useRef(null);
  const ReferenceNumber = React.useRef(null);
  const PremiumPaymentTerm = React.useRef(null);

  let func = () => {
    switch (mode) {
      case "Cheque":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={ChequeNumber} placeholder="Cheque Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={ChequeAccount} placeholder="Cheque Account" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDatetype} onChange={date => setChequeDateType(date)} />
                </div>
              </div>
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={BankName} placeholder="Bank Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>

              </div>
            </div>
          </Fragment>
        );
      case "DD":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" id="exampleInputEmail1" ref={AccountNumber} aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={ChequeNumber} placeholder="Cheque Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={ChequeAccount} placeholder="Cheque Account" />
                </div>
              </div>

              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDatetype} onChange={date => setChequeDateType(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={BankName} placeholder="Bank Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>
          </Fragment>
        );
      case "Online":
        return (
          <Fragment>
            <div className="container">
              <div className="row">

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" id="exampleInputEmail1" ref={AccountNumber} aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Reference Number</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" ref={ReferenceNumber} placeholder="Reference Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>

          </Fragment>
        );
      default:
        return (null);
    }
  }

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = {
      mobile: Number(Mobile.current.value),
      aadhaar: Number(Adhaar.current.value),
      manager_id: Number(RMID.current.value),
      tso_id: Number(TSOID.current.value),
      company: CompanyName.current.value,
      plan_type: PlanType.current.value,
      policy_term: PolicyTerm.current.value,
      premium_payment_term: PremiumPaymentTerm.current.value,
      gross_premium: Number(gross),
      net_premium: parseInt(netAmount), // parseInt
      entry_date: selectedStartDate,
      premium_payment_mode: PPM.current.value,
      payment_mode: ModeOfPayment.current.value,
      business_type: TypeOfBusiness.current.value,
      policy_number: Number(PolicyNumber.current.value),
      payment_term: PaymentTerm.current.value,
      account_number: AccountNumber.current && Number(AccountNumber.current.value),
      cheque_number: ChequeNumber.current && Number(ChequeNumber.current.value),
      cheque_account: ChequeAccount.current && Number(ChequeAccount.current.value),
      cheque_date: ChequeDate.current && ChequeDate.current.value,
      bank_name: BankName.current && BankName.current.value,
      reference: ReferenceNumber.current && ReferenceNumber.current.value,
      stage: stage,
      policy_issue_date: selectedPolicy,
      application_number: ApplicationNumber.current.value,
      revenue: parseInt(String(revenue)) // parseInt
    }

    console.log(formData);

    let res = await request("lifeinsurancetransactions/add").post(formData);
    let json = await res.text();
    if(res.status === 200){
      alert("Transaction Added Successfully");
      props.history.push({pathname: `/Home`});
    }
    else if(res.status === 302){
      alert("Transaction Already Existed");
      props.history.push({pathname: `/Home`});
    }
    console.log(json);

    // checkNullValues(formData);    

  };

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Life Insurance Transaction</h4>
        <div className="headTwo pt-4">
          <form onSubmit={handleSubmit} method="POST">
            <div className="d-flex justify-content-between mt-3">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Title</label>
                <input type="text" className="form-control" name="title" id="exampleInputPassword1" readOnly placeholder={data['title']} defaultValue={data['title']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['firstname']} defaultValue={data['firstname']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['lastname']} defaultValue={data['lastname']} />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Mobile Number</label>
                <input type="text" className="form-control" id="exampleInputPassword1" ref={Mobile} readOnly placeholder={data['mobile']} defaultValue={data['mobile']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
                <input type="email" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['email']} defaultValue={data['email']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder={new Date(data['birthdate']).toLocaleDateString()}
                  defaultValue={new Date(data['birthdate']).toLocaleDateString()}
                  readOnly
                />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Pan Card</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['pan']} defaultValue={data['pan']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Aadhaar Number</label>
                <input type="text" className="form-control" id="exampleInputPassword1" ref={Adhaar} readOnly placeholder={data['aadhaar']} defaultValue={data['aadhaar']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Location</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['location']} defaultValue={data['location']} />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Branch Name</label>
                <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['branch']} defaultValue={data['branch']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            </div>

            <div className="d-flex justify-content-center">
              <h5>Present Address</h5>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_line1']} defaultValue={data['present_line1']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_line2']} defaultValue={data['present_line2']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_city']} defaultValue={data['present_city']} />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_district']} defaultValue={data['present_district']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />

              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
                <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_pincode']} defaultValue={data['present_pincode']} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">

              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">

              </div>
            </div>

            <div className="d-flex justify-content-center">
              <h5>Business Entry Details</h5>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">RelationShip Manger ID</label>
                <select className="form-control" name="rm_id" ref={RMID} >
                  <option value="">select RMID</option>
                  <option value="50018">50018 (Umamaheswar Rao)</option>
                  <option value="50001">50001 (Manohar Reddy)</option>
                  <option value="50015">50015 (Radha Krishna)</option>
                  <option value="50017">50017 (Venkatesham)</option>
                  <option value="50010">50010 (Navya)</option>
                  <option value="50016">50016 (Harish)</option>
                  <option value="50020">50020 (Reena)</option>
                  <option value="50019">50019 (Prashanthi)</option>
                  <option value="50021">50021 (Mahesh)</option>
                  <option value="50022">50022 (Sana)</option>
                  <option value="50027">50027 (Naresh)</option>
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">TSO ID</label>
                <select className="form-control" name="tso_id" ref={TSOID}>
                  <option value="50020">select TSOID</option>
                  <option value="50019">50019 (Prashanthi)</option>
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Application Number</label>
                <input type="text" className="form-control" name="application_number" id="exampleInputEmail1" ref={ApplicationNumber} aria-describedby="emailHelp" placeholder="Application Number" />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Company Name</label>
                <select className="form-control" ref={CompanyName} name="company" id="empProductName" style={{ width: "100%" }} onChange={(e) => fetchPlans(e)}>
                  <option value="">Select Company</option>
                  {
                    companies.map((company, index) => {
                      return <option key={index + company} value={company}>{company}</option>
                    })
                  }
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Plan Type</label>

                <select className="form-control" name="plan_type" aria-required="true" aria-invalid="false" ref={PlanType} onChange={(e) => setPlans(e.target.value)}>
                  <option value="">Select plan type</option>
                  <option value="ULIPs">ULIPs</option>
                  <option value="Traditional plan">Traditional plan</option>
                </select>

              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Plan Name</label>
                <select className="form-control" name="plan_name" aria-required="true" aria-invalid="false" ref={PlanType} onChange={(e) => fetchPpt(e)}>
                  <option value="">Select plan Name</option>
                  {
                    companyPlans.map((company, index) => {
                      return <option key={index + company} value={company}>{company}</option>
                    })
                  }
                </select>
              </div>

            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Premium Payment Term</label>
                <select className="form-control" name="premium_payment_term" id="empProductName" ref={PremiumPaymentTerm} onChange={(e) => getRevenue(netAmount, e.target.value, setRevenue)}>
                  <option value="">Select PPT</option>
                  {
                    ppt.map((item, index) => {
                      return <option key={index + item.premium_payment_term} value={item.revenue}>{item.premium_payment_term}</option>
                    })
                  }
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Policy Term</label>
                <input type="text" className="form-control" name="policy_term" id="exampleInputPassword1" ref={PolicyTerm} placeholder="Policy Term" />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Gross Premium</label>
                <input
                  type="text"
                  className="form-control"
                  name="gross_premium"
                  id="exampleInputPassword1"
                  placeholder="Gross Premium"
                  value={gross}
                  ref={GrossPremium}
                  onChange={(e) => getNetAmount(e.target.value, setNetAmount)}
                />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Net Premium</label>
                <input type="text" className="form-control" id="exampleInputEmail1" value={netAmount} ref={NetPremium} aria-describedby="emailHelp" placeholder="Net Premium" readOnly />
              </div>

              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Date of Entry</label>
                <DatePicker className="form-control" selected={dateOfEntry} ref={DateofEntry} onChange={date => setDate(date)} />
              </div>

              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Premium Payment Mode</label>
                <select className="form-control" name="product" id="empProductName" ref={PPM}>
                  <option value="">Select PPM</option>
                  <option value="Monthly">Monthly</option>
                  <option value="Quarterly">Quarterly</option>
                  <option value="Half Yearly">Half Yearly</option>
                  <option value="Anniversary">Anniversary</option>
                </select>
              </div>
            </div>

            <div className="d-flex justify-content-between">
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Type Of Business</label>
                <select className="form-control" name="product" id="empProductName" ref={TypeOfBusiness} onChange={(e) => setBusinessType(e.target.value)}>
                  <option value="">Select Business</option>
                  <option value="Fresh">New business</option>
                  <option value="Renewal">Renewel</option>
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Mode Of Payment</label>
                <select className="form-control" name="product" id="empProductName" onChange={(e) => setMode(e.target.value)} ref={ModeOfPayment}>
                  <option value="">Select MOP</option>
                  <option value="Cheque">Cheque</option>
                  <option value="DD">DD</option>
                  <option value="Online">Online</option>
                  <option value="Cash">Cash</option>
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Policy Number</label>
                <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Policy Number" ref={PolicyNumber} />
              </div>
            </div>

            <div className="d-flex justify-content-between">
              {func()}
            </div>

            <div className="d-flex justify-content-between">

              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Stage</label>
                <select className="form-control" name="premium_payment_mode" ref={PaymentTerm} onChange={(e) => setStage(e.target.value)}>
                  <option value="">Select Stage</option>
                  <option value="Monthly">Policy Issued</option>
                  <option value="Quarterly">Policy Cancelled</option>
                  <option value="Half Yearly">Policy Underride</option>
                </select>
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Policy Issue Date</label>
                <DatePicker className="form-control" selected={policyDate} onChange={date => setPolicy(date)} ref={PolicyIssueDate} />
              </div>
              <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Revenue</label>
                <input type="text" className="form-control" id="exampleInputPassword1" ref={Revenue} placeholder="Revenue" readOnly value={revenue} />
              </div>
            </div>

            <div className="d-flex" style={{ width: "100%" }}>
              <button
                type="submit"
                className="btn btn-primary d-block btn-sm mx-auto"
                style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
              >
                Submit
              </button>
            </div>
          </form>
        </div>
      </section>
    </Fragment>
  );
}
export default withRouter(Example);