import React, { Fragment, useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { request } from '../helper';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const Example = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [selectedStartDate, setSelectedStartDate] = useState('');
  const [selectedEndDate, setSelectedEndDate] = useState('');
  const [zonal, setZonal] = useState([]);
  const [selectedZonal, setSelectedZonal] = useState('');
  const [data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      let res = await request("lifeInsuranceZonalReports/fetchall").get();
      let json = await res.json();

      setZonal(json);
    })();
  }, []);

  let dateFormat = (date) => {
    let [month, day, year] = date.split("/");

    if (month > Number(12)) {
      return `${month}-${day}-${year}`
    }

    return `${day}-${month}-${year}`;
  }

  let handleChange = (e) => {
    setSelectedZonal(e.target.value);
  }

  let selectedDate = (date, func) => {
    func(date);
  }

  let submit = async () => {
    var Obj = {
      location: selectedZonal,
      start_date: dateFormat(new Date(startDate).toLocaleDateString()),
      end_date: dateFormat(new Date(endDate).toLocaleDateString()),
    }

    console.log(Obj);

    let res = await request("zonalRevenueReports/fetch").post(Obj);
    let json = await res.json();

    setData(json);
  }

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Revenue Zonal Reports</h4>
        <div className="headTwo pt-4">
          <div className="d-flex justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Select Zonal</label>
              <select className="form-control" name="sub_option" id="mop" aria-required="true" aria-invalid="false" onChange={(e) => handleChange(e)}>
                <option value="">Select Zonal</option>
                {
                  zonal.map((item, index) => {
                    return (
                      <option key={index + item} value={item}>{item}</option>
                    )
                  })
                }
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">From Date</label>
              <DatePicker className="form-control" selected={startDate} onChange={date => selectedDate(date, setStartDate)} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">To Date</label>
              <DatePicker className="form-control" selected={endDate} onChange={date => selectedDate(date, setEndDate)} />
            </div>
          </div>
          <div className="d-flex" style={{ width: "100%" }}>
            <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
              onClick={() => submit()}>Submit</button>
          </div>
        </div>
      </section>

      <table className="table table-bordered">
        <thead>
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Date</th>
            <th scope="col">Life</th>
            <th scope="col">Vehicle</th>
            <th scope="col">Health</th>
            <th scope="col">SIP</th>
            <th scope="col">STP</th>
            <th scope="col">SWP</th>
            <th scope="col">LumpSum</th>
            <th scope="col">Fixed</th>
            <th scope="col">Total</th>
          </tr>
        </thead>
        <tbody>
          {
            (data !== undefined) &&
            data.map((item, index) => {
              return (
                <tr key={index}>
                  <th scope="row">{index + 1}</th>
                  <td><small>{item.entry_date}</small></td>
                  <td><small>{item.lifeInsuranceRevenue}</small></td>
                  <td><small>{item.vehicle}</small></td>
                  <td><small>{item.health}</small></td>
                  <td><small>{item.SIP}</small></td>
                  <td><small>{item.STP}</small></td>
                  <td><small>{item.SWP}</small></td>
                  <td><small>{item.LumpSum}</small></td>
                  <td><small>{item.fixedDeposits}</small></td>
                  <td><small>{item.total}</small></td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Fragment>

  );
}


export default withRouter(Example);