import React, { Fragment, useState, useEffect, useRef } from "react";
import DatePicker from "react-datepicker";
import { withRouter } from 'react-router-dom';
import { request } from '../helper';
import "react-datepicker/dist/react-datepicker.css";
import "react-datepicker/dist/react-datepicker.css";

const Example = (props) => {
  const [policyDate, setPolicyDate] = useState(new Date());
  const [dateOfEntry, setDateOfEntry] = useState(new Date());
  const [chequeDate, setChequeDate] = useState(new Date());
  const [data, setData] = useState({});
  const [type, setType] = useState([]);
  const [revenue, setRevenue] = useState(0);
  const [netAmount, setNetAmount] = useState(0);
  const [companies, setCompany] = useState([]);
  const [plans, setPlans] = useState([]);
  const [ppt, setPPT] = useState([]);
  const [mode, setMode] = useState([]);
  const [toimode, setTOIMode] = useState('');
  const [products, setProduct] = useState([]);
  const [selected, setSelected] = useState({});
  const [modes, setModes] = useState('');
  const [revenuePerc, setRevenuePerc] = useState('');
  const [gross, setGross] = useState('');
  const [state, setState] = useState({});

  const Mobile = React.useRef(null);
  const Aadhaar = React.useRef(null);

  useEffect(() => {
    if (props.location.state === undefined) {
      props.history.push('/Home/generalinsurancetransaction');
      return;
    }

    setData(props.location.state);

    (async () => {
      let res = await request('generalinsurancerevenue/columns').get();
      setCompany(await res.json());
    })();
  }, []);

  let fetchProduct = async ({ target: { value } }) => {
    let res = await request(`generalinsurancerevenue/product/${value}`).get();
    setProduct(await res.json());
    setSelected({ company: value });
  }

  let fetchType = async ({ target: { value } }) => {
    let res = await request(`generalinsurancerevenue/type/${selected["company"]}/${value}`).get();
    let json = await res.json();
    setType(json);
    setSelected({ product: value, ...selected });
  }

  let fetchMode = async ({ target: { value } }) => {
    let res = await request(`generalinsurancerevenue/type/${selected["company"]}/${selected["product"]}/${value}`).get();
    let json = await res.json();
    setMode(json);
    setSelected({ type: value, ...selected });
  }

  let check = () => {
    if (products.length === 0) {
      alert("Please select a Company");
      return false;
    } else if (type.length === 0) {
      alert("Please select a Type");
      return false;
    } else if (mode.length === 0) {
      alert("Please select a Mode");
      return false;
    } else if (revenuePerc === '') {
      alert("Please select a Mode of Insurance");
      return false;
    }

    return true;
  }

  let fetchNetAmount = (amount) => {

    if (!check()) return;

    setGross(amount);
    setNetAmount(Number(amount) / 100 * 18);
    setRevenue((Number(amount) / 100 * 18) / 100 * Number(revenuePerc));
  }

  let func = () => {
    switch (modes) {
      case "Cheque":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" name="cheque_number" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Cheque Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" name="cheque_account" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Cheque Account" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDate} onChange={date => setChequeDate(date)} />
                </div>
              </div>
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" name="bank_name" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Bank Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>

              </div>
            </div>
          </Fragment>
        );
      case "DD":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" name="account_number" onChange={(e) => changeHandle(e)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" name="cheque_number" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Cheque Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" name="cheque_account" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Cheque Account" />
                </div>
              </div>

              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDate} onChange={date => setChequeDate(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" name="bank_name" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Bank Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>
          </Fragment>
        );
      case "Online":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" name="account_number" onChange={(e) => changeHandle(e)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Reference Number</label>
                  <input type="text" className="form-control" name="reference" onChange={(e) => changeHandle(e)} id="exampleInputPassword1" placeholder="Reference Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>

          </Fragment>
        );
      default:
        return (null);
    }
  }

  let changeHandle = ({ target: { value, name } }) => {
    let obj = state;

    obj[name] = value;

    setState(obj);

    console.log(state);
  }

  let submit = async () => {
    console.log(selected);
    var req = {
      ...state,
      gross_premium: Number(gross),
      net_premium: parseInt(String(netAmount)),
      revenue: parseInt(String(revenue)),
      insurance_type: selected["type"],
      company: selected["company"],
      product: selected["product"],
      mobile: Number(Mobile.current.value),
      aadhaar: Number(Aadhaar.current.value),
      account_number: Number(state["account_number"]), // NAN
      cheque_number: Number(state["cheque_number"]), // NAN
    }

    let res = await request("generalinsurancetransactions/add").post(req);
    let json = await res.text();
    if(res.status === 200){
      alert("Transaction Added Successfully");
      props.history.push({pathname: `/Home`});
    }
    else if(res.status === 302){
      alert("Transaction Already Existed");
      props.history.push({pathname: `/Home`});
    }
  }

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add General Insurance Transaction</h4>
        <div className="headTwo pt-4">

          <div className="d-flex justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Title</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['title']} defaultValue={data['title']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['firstname']} defaultValue={data['firstname']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['lastname']} defaultValue={data['lastname']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mobile Number</label>
              <input type="text" className="form-control" id="exampleInputPassword1" ref={Mobile} readOnly placeholder={data['mobile']} defaultValue={data['mobile']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
              <input type="email" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['email']} defaultValue={data['email']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
              <input
                type="text"
                className="form-control"
                id="exampleInputPassword1"
                placeholder={new Date(data['birthdate']).toLocaleDateString()}
                defaultValue={new Date(data['birthdate']).toLocaleDateString()}
                readOnly
              />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Pan Card</label>
              <input type="number" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['pan']} defaultValue={data['pan']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Aadhaar Number</label>
              <input type="text" className="form-control" id="exampleInputPassword1" ref={Aadhaar} readOnly placeholder={data['aadhaar']} defaultValue={data['aadhaar']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Location</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['location']} defaultValue={data['location']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Branch Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['branch']} defaultValue={data['branch']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          </div>

          <div className="d-flex justify-content-center">
            <h5>Present Address</h5>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_line1']} defaultValue={data['present_line1']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_line2']} defaultValue={data['present_line2']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_city']} defaultValue={data['present_city']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_district']} defaultValue={data['present_district']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_pincode']} defaultValue={data['present_pincode']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          </div>

          <div className="d-flex justify-content-center">
            <h5>Business Entry Details</h5>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">RelationShip Manger ID</label>
              <select className="form-control" name="manager_id" onChange={(e) => changeHandle(e)}>
                <option value="">select RMID</option>
                <option value="50018">50018 (Umamaheswar Rao)</option>
                <option value="50001">50001 (Manohar Reddy)</option>
                <option value="50015">50015 (Radha Krishna)</option>
                <option value="50017">50017 (Venkatesham)</option>
                <option value="50010">50010 (Navya)</option>
                <option value="50016">50016 (Harish)</option>
                <option value="50020">50020 (Reena)</option>
                <option value="50019">50019 (Prashanthi)</option>
                <option value="50021">50021 (Mahesh)</option>
                <option value="50022">50022 (Sana)</option>
                <option value="50027">50027 (Naresh)</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">TSO ID</label>
              <select className="form-control" name="tso_id" onChange={(e) => changeHandle(e)}>
                <option value="">select TSOID</option>
                <option value="50020">50020 (Reena)</option>
                <option value="50019">50019 (Prashanthi)</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Company Name</label>
              <select className="form-control" name="company" id="empProductName" style={{ width: "100%" }} onChange={(e) => fetchProduct(e)}>
                <option value="">Select Company</option>
                {
                  companies.map((company, index) => {
                    return <option key={index + company} value={company}>{company}</option>
                  })
                }
              </select>
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Product Name</label>
              <select className="form-control" name="product" id="empProductName" style={{ width: "100%" }} onChange={(e) => fetchType(e)}>
                <option value="">Select Product</option>
                {
                  products.map((company, index) => {
                    return <option key={index + company} value={company}>{company}</option>
                  })
                }
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Type of Insurance</label>
              <select className="form-control" name="insurance_type" onChange={(e) => fetchMode(e)}>
                <option value="">Select TOI</option>
                {
                  type.map((item, index) => {
                    return <option key={index + item} value={item}>{item} Insurance</option>
                  })
                }
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mode of Insurance</label>
              <select className="form-control" name="insurance_mode" onChange={e => setRevenuePerc(e.target.value)}>
                <option value="">Select MOI</option>
                {
                  mode.map((item, index) => {
                    return <option key={index + item.revenue} value={item.revenue}>{item.mode}</option>
                  })
                }
              </select>
            </div>


          </div>

          <div className="d-flex justify-content-between">

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Gross Premium</label>
              <input
                type="text"
                className="form-control"
                name="gross_premium"
                id="exampleInputPassword1"
                value={gross}
                placeholder="Gross Premium"
                onChange={(e) => fetchNetAmount(e.target.value)}
              />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Net Premium</label>
              <input
                type="text"
                className="form-control"
                name="net_premium"
                id="exampleInputPassword1"
                value={netAmount}
                readOnly
                placeholder="Net Premium"
              />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Policy Number</label>
              <input
                type="text"
                className="form-control"
                onChange={(e) => changeHandle(e)}
                name="policy_number"
                id="exampleInputPassword1"
                placeholder="Policy Number"
              />
            </div>


          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Policy Type</label>
              <input type="text" className="form-control" onChange={(e) => changeHandle(e)} name="policy_type" id="exampleInputPassword1" placeholder="Policy Type" />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Policy Tenour</label>
              <input type="email" className="form-control" onChange={(e) => changeHandle(e)} name="policy_tenour" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Policy Tenour" />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Date Of Policy Login</label>
              <DatePicker className="form-control" selected={policyDate} onChange={date => setPolicyDate(date)} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Type Of Business</label>
              <select className="form-control" name="business_type" id="empProductName" onChange={(e) => changeHandle(e)}>
                <option value="">Select Business</option>
                <option value="Fresh">New business</option>
                <option value="Renewal">Renewel</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mode Of Payment</label>
              <select className="form-control" name="payment_mode" id="mop" aria-required="true" aria-invalid="false" onChange={(e) => setModes(e.target.value)} >
                <option value="">Select MOP</option>
                <option value="Cheque">Cheque</option>
                <option value="DD">DD</option>
                <option value="Online">Online</option>
                <option value="Cash">Cash</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Stage</label>
              <select className="form-control" name="stage" onChange={(e) => changeHandle(e)}>
                <option value="">Select Stage</option>
                <option value="Monthly">Policy Issued</option>
                <option value="Quarterly">Policy Cancelled</option>
                <option value="Half Yearly">Policy Underride</option>
              </select>
            </div>
          </div>

          <div className="d-flex justify-content-between">
            {func()}
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Date of Entry</label>
              <DatePicker className="form-control" selected={dateOfEntry} onChange={date => setDateOfEntry(date)} />
            </div>


            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Revenue</label>
              <input type="text" className="form-control" name="revenue" id="exampleInputPassword1" placeholder="Revenue" readOnly value={revenue} />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          </div>

          <div className="d-flex" style={{ width: "100%" }}>
            <button
              type="submit"
              onClick={() => submit()}
              className="btn btn-primary d-block btn-sm mx-auto"
              style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
            >
              Submit
            </button>
          </div>

        </div>
      </section>
    </Fragment>

  );
}

export default withRouter(Example)
