import React, { Fragment, useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { request } from '../helper';
import { withRouter } from 'react-router-dom';

const Example = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  const [chequeDate, setChequeDate] = useState(new Date());
  const [sipStartDate, setSipStart] = useState(new Date());
  const [sipEndDate, setSipEnd] = useState(new Date());
  const [stpStartDate, setStpStart] = useState(new Date());
  const [stpEndDate, setStpEnd] = useState(new Date());
  const [dateOfIssuance, setDateOfIssuance] = useState(new Date());
  const [dateOfEntry, setDateOfEntry] = useState(new Date());
  const [swpStartDate, setSwpStart] = useState(new Date());
  const [swpEndDate, setSwpEnd] = useState(new Date());
  const [chequeDateType, setChequeDateType] = useState(new Date());
  const [data, setData] = useState('');
  const [companies, setCompanies] = useState([]);
  const [funds, setFunds] = useState([]);
  const [plans, setPlans] = useState([]);
  const [options, setOptions] = useState([]);
  const [mode, setMode] = useState([]);
  const [subOpt, setSubOpt] = useState([]);
  const [selected, setSelected] = useState({});
  const [revenuePerc, setRevenuePerc] = useState(0);
  const [revenue, setRevenue] = useState(0);
  const [option, setOption] = useState('');
  const [mutualMode, setMutualMode] = useState('');
  const [modeOfPay, setModeOfPay] = useState('');
  const [state, setState] = useState({});
  const [selectedIssuanceDate, setSelectedIssuanceDate] = useState('');
  const [selectedEntryDate, setSelectedEntryDate] = useState('');
  const [selectedSWPStart, setSelectedSWPStart] = useState('');
  const [selectedSWPEnd, setSelectedSWPEnd] = useState('');
  const [selectedSIPStart, setSelectedSIPStart] = useState('');
  const [selectedSIPEnd, setSelectedSIPEnd] = useState('');
  const [selectedSTPStart, setSelectedSTPStart] = useState('');
  const [selectedSTPEnd, setSelectedSTPEnd] = useState('');
  const [amount, setAmount] = useState('');

  const Mobile = React.useRef(null);
  const Aadhaar = React.useRef(null);
  const Branch = React.useRef(null);

  useEffect(() => {
    if (props.location.state === undefined) {
      console.log(props.location.state);
      props.history.push('/Home/generalinsurancetransaction');
      return;
    }

    setData(props.location.state);
    (async () => {
      let res = await request('mutualfundsrevenue/columns').get();
      setCompanies(await res.json());
    })();
  }, []);

  let dateFormat = (date) => {
    let [month, day, year] = date.split("/");

    if (month > Number(12)) {
      return `${month}-${day}-${year}`
    }

    return `${day}-${month}-${year}`;
  }

  let selectCompany = async ({ target: { value } }) => {
    let res = await request(`mutualfundsrevenue/funds/${value}`).get();
    setFunds(await res.json());
    setSelected({ company: value });
  }

  let selectFunds = async ({ target: { value } }) => {
    const COMPANY = selected['company'];
    const RES = await request(`mutualfundsrevenue/plan/${COMPANY}/${value}`).get();
    setPlans(await RES.json());
    setSelected({ funds: value, ...selected });
  }

  let selectPlan = async ({ target: { value } }) => {
    const FUND = selected['funds'];
    const COMPANY = selected['company'];
    const RES = await request(`mutualfundsrevenue/option/${COMPANY}/${value}/${FUND}`).get();
    setOptions(await RES.json());

    console.log(options);

    setSelected({ plan: value, ...selected });
  }

  let selectOption = async ({ target: { value } }) => {
    const FUND = selected['funds'];
    const PLAN = selected['plan'];
    const COMPANY = selected['company'];
    console.log(FUND);
    console.log(PLAN);
    console.log(COMPANY);
    console.log(value);
    const RES = await request(`mutualfundsrevenue/revenue/${COMPANY}/${PLAN}/${FUND}/${value}`).get();
    const R = await RES.json();
    console.log(R);
    setMode(R)

    if (R.length > 0) {
      setRevenuePerc(Number(R[0].revenue));
    }
    setSelected({ option: value, ...selected });
  }

  let fetchRevenue = ({ target: { value } }) => {
    console.log(revenuePerc, value);
    setAmount(value);
    setRevenue((Number(value) / 100 * Number(revenuePerc)));
  }

  let changeHandler = ({ target: { value, name } }) => {
    let obj = state;

    obj[name] = value;

    setState(obj);

    console.log(state);
  }

  let func = () => {
    switch (option) {
      case "Dividend":
        return (
          <Fragment>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Sub Option</label>
              <select className="form-control" name="sub_option" id="mop" aria-required="true" aria-invalid="false">
                <option value="">Select Sub Option</option>
                <option value="">Dividend Investment</option>
                <option value="">Dividend Payout</option>
              </select>
            </div>
          </Fragment>
        );
      default:
        return (
          <Fragment>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            </div>
          </Fragment>
        );
    }
  }

  let mutualModefunc = () => {
    switch (mutualMode) {
      case "Lumpsum":
        return (null);
      case "SWP":
        return (
          <Fragment>
            <div className="container">
              <div className="row">

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">SWP Start Date</label>
                  <DatePicker className="form-control" selected={swpStartDate} onChange={date => setSWPStart(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">SWP End Date</label>
                  <DatePicker className="form-control" selected={swpEndDate} onChange={date => setSWPEnd(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">SWP Months</label>
                  <input type="text" className="form-control" name="swp_month" id="exampleInputPassword1" placeholder="SWP Months" onChange={e => changeHandler(e)} />
                </div>

              </div>
            </div>

          </Fragment>
        );

      case "SIP":
        return (
          <Fragment>
            <div className="container">
              <div className="row">

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">SIP Start Date</label>
                  <DatePicker className="form-control" selected={sipStartDate} onChange={date => setSIPStart(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">SIP End Date</label>
                  <DatePicker className="form-control" selected={sipEndDate} onChange={date => setSIPEnd(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">SIP Months</label>
                  <input type="text" className="form-control" name="sip_month" id="exampleInputPassword1" placeholder="SiP Months" onChange={e => changeHandler(e)} />
                </div>

              </div>
            </div>

          </Fragment>
        );

      case "STP":
        return (
          <Fragment>
            <div className="container">
              <div className="row">

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">STP Start Date</label>
                  <DatePicker className="form-control" selected={stpStartDate} onChange={date => setSTPStart(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">STP End Date</label>
                  <DatePicker className="form-control" selected={stpEndDate} onChange={date => setSTPEnd(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">STP Months</label>
                  <input type="text" className="form-control" name="stp_month" id="exampleInputPassword1" placeholder="STP Months" onChange={e => changeHandler(e)} />
                </div>

              </div>
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Old Fund Name</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Old Fund Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
              </div>
            </div>

          </Fragment>
        );


      default:
        return (
          <Fragment>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            </div>
          </Fragment>
        );
    }
  }

  let modeOfPayfunc = () => {
    switch (modeOfPay) {
      case "Cheque":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Cheque Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Cheque Account" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDateType} onChange={date => setChequeDateType(date)} />
                </div>
              </div>
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Bank Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>

              </div>
            </div>
          </Fragment>
        );
      case "DD":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Cheque Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Cheque Account" />
                </div>
              </div>

              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDateType} onChange={date => setChequeDateType(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Bank Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>
          </Fragment>
        );
      case "Online":
        return (
          <Fragment>
            <div className="container">
              <div className="row">

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Reference Number</label>
                  <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Reference Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>

          </Fragment>
        );
      default:
        return (null);
    }
  }

  let setIssuanceDate = (date) => {
    setDateOfIssuance(date);
    setSelectedIssuanceDate(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    );
  }

  let setEntryDate = (date) => {
    setDateOfEntry(date);
    setSelectedEntryDate(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    );
  }

  let setSWPStart = (date) => {
    setSwpStart(date)
    setSelectedSWPStart(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    )
  }

  let setSWPEnd = (date) => {
    setSwpEnd(date)
    setSelectedSWPEnd(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    )
  }

  let setSIPStart = (date) => {
    setSipStart(date)
    setSelectedSIPStart(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    )
  }

  let setSIPEnd = (date) => {
    setSipEnd(date)
    setSelectedSIPEnd(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    )
  }

  let setSTPStart = (date) => {
    setStpStart(date)
    setSelectedSTPStart(
      // dateFormat(
      new Date(date).toLocaleDateString()
      //)
    )
  }

  let setSTPEnd = (date) => {
    setStpEnd(date)
    setSelectedSTPEnd(
      // dateFormat(
      new Date(date).toLocaleDateString()
      // )
    )
  }

  let check = () => {
    if (isNaN(Number(Mobile.current.value))) {
      alert("Please enter valid mobile number");
      return false;
    } else if (isNaN(Number(Aadhaar.current.value))) {
      alert("Please Enter valid aadhaar Number");
      return false;
    } else if (isNaN(Number(state["folio_number"]))) {
      alert("Please enter valid Folio Number");
      return false;
    }

    return true;
  }

  let submit = async () => {
    let reqBody = {
      ...state,
      mobile: Number(Mobile.current.value),
      aadhaar: Number(Aadhaar.current.value),
      branch: Branch.current.value,
      issuance_date: selectedIssuanceDate || dateFormat(new Date().toLocaleDateString()),
      entry_date: selectedEntryDate || dateFormat(new Date().toLocaleDateString()),
      stp_start_date: selectedSTPStart || dateFormat(new Date().toLocaleDateString()),
      stp_end_date: selectedSTPEnd || dateFormat(new Date().toLocaleDateString()),
      swp_start_date: selectedSWPStart || dateFormat(new Date().toLocaleDateString()),
      swp_end_date: selectedSWPEnd || dateFormat(new Date().toLocaleDateString()),
      sip_start_date: selectedSIPStart || dateFormat(new Date().toLocaleDateString()),
      sip_end_date: selectedSIPEnd || dateFormat(new Date().toLocaleDateString()),
      amount: Number(amount),
      revenue: parseInt(String(revenue)),
      folio_number: 123,
      amc: selected["company"],
      fund: selected["funds"],
      plan: selected["plan"],
      option: selected["option"],
      payment_mode: modeOfPay,
      mode: mutualMode
    };

    if (check() === false) return;

    console.log(reqBody);

    let res = await request('mutualfundstransactions/add').post(reqBody);
    let json = await res.text();

    console.log(json);
  }

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Mutual Fund Transaction</h4>
        <div className="headTwo pt-4">

          <div className="d-flex justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Title</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['title']} defaultValue={data['title']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['firstname']} defaultValue={data['firstname']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['lastname']} defaultValue={data['lastname']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mobile Number</label>
              <input type="text" className="form-control" id="exampleInputPassword1" ref={Mobile} readOnly placeholder={data['mobile']} defaultValue={data['mobile']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
              <input type="email" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['email']} defaultValue={data['email']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
              <DatePicker className="form-control" selected={startDate} onChange={date => setStartDate(date)} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Pan Card</label>
              <input type="number" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['pan']} defaultValue={data['pan']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Aadhaar Number</label>
              <input type="text" className="form-control" id="exampleInputPassword1" ref={Aadhaar} readOnly placeholder={data['aadhaar']} defaultValue={data['aadhaar']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Location</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['location']} defaultValue={data['location']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Branch Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" ref={Branch} readOnly placeholder={data['branch']} defaultValue={data['branch']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          </div>

          <div className="d-flex justify-content-center">
            <h5>Present Address</h5>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_line1']} defaultValue={data['present_line1']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_line2']} defaultValue={data['present_line2']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_city']} defaultValue={data['present_city']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_district']} defaultValue={data['present_district']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
              <input type="text" className="form-control" id="exampleInputEmail1" readOnly placeholder={data['present_pincode']} defaultValue={data['present_pincode']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            </div>
          </div>

          <div className="d-flex justify-content-center">
            <h5>Business Entry Details</h5>
          </div>

          <div className="d-flex justify-content-center">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">RelationShip Manger ID</label>
              <select className="form-control" name="manager_id" onChange={e => changeHandler(e)}>
                <option value="">select RMID</option>
                <option value="50018">50018 (Umamaheswar Rao)</option>
                <option value="50001">50001 (Manohar Reddy)</option>
                <option value="50015">50015 (Radha Krishna)</option>
                <option value="50017">50017 (Venkatesham)</option>
                <option value="50010">50010 (Navya)</option>
                <option value="50016">50016 (Harish)</option>
                <option value="50020">50020 (Reena)</option>
                <option value="50019">50019 (Prashanthi )</option>
                <option value="50021">50021 (Mahesh)</option>
                <option value="50022">50022 (Sana)</option>
                <option value="50027">50027 (Naresh )</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">TSO ID</label>
              <select className="form-control" name="tso_id" onChange={e => changeHandler(e)}>
                <option value="">select TSOID</option>
                <option value="50020">50020 (Reena)</option>
                <option value="50019">50019 (Prashanthi )</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Product Name</label>
              <select className="form-control" name="product" id="empProductName" style={{ width: "100%" }} onChange={e => changeHandler(e)}>
                <option value="">Select Product</option>
                <option value="Mutual Funds">Mutual Funds</option>
              </select>
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">AMC Name</label>
              <select className="form-control" name="amc" id="empProductName" style={{ width: "100%" }} onChange={(e) => selectCompany(e)}>
                <option value="">Select AMC</option>
                {companies.map((company, index) => <option key={index + company}>{company}</option>)}
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Fund Name</label>
              <select className="form-control" name="fund" id="empProductName" style={{ width: "100%" }} onChange={(e) => selectFunds(e)}>
                <option value="">Select Funds</option>
                {funds.map((fund, index) => <option key={index + fund}>{fund}</option>)}
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Plan Name</label>
              <select className="form-control" name="plan" id="empProductName" style={{ width: "100%" }} onChange={(e) => selectPlan(e)}>
                <option value="">Select Plan</option>
                {plans.map((plan, index) => <option key={index + plan}>{plan}</option>)}
              </select>
            </div>

          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Option</label>
              <select className="form-control" name="option" id="empProductName" style={{ width: "100%" }} onChange={(e) => selectOption(e)} >
                <option value="">Select Option</option>
                <option value="Growth">Growth</option>
                <option value="Dividend">Dividend</option>
              </select>
            </div>

            {func()}

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mode</label>
              <select className="form-control" name="mode" id="empProductName" style={{ width: "100%" }} onChange={(e) => setMutualMode(e.target.value)} >
                <option value="">Select Mode</option>
                <option value="Lumpsum">Lumpsum</option>
                <option value="SWP">SWP</option>
                <option value="SIP">SIP</option>
                <option value="STP">STP</option>
              </select>
            </div>
          </div>

          <div className="d-flex justify-content-between">
            {mutualModefunc()}
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Type Of Business</label>
              <select className="form-control" name="type_of_entry">
                <option value="">Select Business</option>
                <option value="Fresh">New business</option>
                <option value="Renewal">RollOver</option>
                <option value="Renewal">Port</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mode Of Payment</label>
              <select className="form-control" name="mode_of_payment" id="mop" aria-required="true" aria-invalid="false" onChange={(e) => setModeOfPay(e.target.value)}>
                <option value="">Select MOP</option>
                <option value="Cheque">Cheque</option>
                <option value="DD">DD</option>
                <option value="Online">Online</option>
                <option value="Cash">Cash</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Amount</label>
              <input type="email" className="form-control" id="exampleInputEmail1" placeholder="Amount" onChange={(e) => fetchRevenue(e)} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            {modeOfPayfunc()}
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Folio Number</label>
              <input type="text" className="form-control" id="exampleInputPassword1" name="folio_number" placeholder="Folio Name" onChange={e => changeHandler(e)} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Stage</label>
              <select className="form-control" name="premium_payment_mode">
                <option value="">Select Stage</option>
                <option value="Monthly">Policy Issued</option>
                <option value="Quarterly">Policy Cancelled</option>
                <option value="Half Yearly">Policy Underride</option>
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Date of Issuance</label>
              <DatePicker className="form-control" selected={dateOfIssuance} onChange={date => setIssuanceDate(date)} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Date of Entry</label>
              <DatePicker className="form-control" selected={dateOfEntry} onChange={date => setEntryDate(date)} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Revenue</label>
              <input type="text" className="form-control" id="exampleInputPassword1" value={revenue} placeholder="Revenue" readOnly />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            </div>
          </div>
          <div className="d-flex" style={{ width: "100%" }}>
            <button
              type="submit"
              onClick={() => submit()}
              className="btn btn-primary d-block btn-sm mx-auto"
              style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
            >
              Submit
            </button>
          </div>
        </div>

      </section>
    </Fragment>
  );
}
export default withRouter(Example);