import React, { Fragment, useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
import { request } from '../helper';

const Example = () => {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [selectedStart, setSelectedStart] = useState('');
  const [selectedEnd, setSelectedEnd] = useState('');
  const [data, setData] = useState([]);
  const [RM, setRM] = useState([]);
  const [selectedRM, setSelectedRM] = useState('');

  useEffect(() => {
    (async function () {
      const RESPONSE = await request('lifeInsuranceRMReport/fetchRM').get();
      const JSON = await RESPONSE.json();

      setRM(JSON);
    })();
  }, []);

  let dateFormat = (date) => {
    let [month, day, year] = date.split("/");

    if (month > Number(12)) {
      return `${month}-${day}-${year}`
    }

    return `${day}-${month}-${year}`;
  }

  let dateChange = (e) => {
    setStartDate(e);
    setSelectedStart(dateFormat(new Date(e).toLocaleDateString()));
  }

  let endDateChange = (e) => {
    setEndDate(e);
    setSelectedEnd(dateFormat(new Date(e).toLocaleDateString()));
  }

  let fetchZonal = async ({ target: { value } }) => {
    console.log(JSON.parse(value));
    setSelectedRM(JSON.parse(value));
  }

  let submit = async () => {
    var requestObj = {
      location: selectedRM["location"],
      branch: selectedRM["branch"],
      start_date: selectedStart,
      end_date: selectedEnd,
    }

    let res = await request('fixedDepositRmReport/fetchReports').post(requestObj);
    let json = await res.json();

    setData(json);
  }

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Fixed Deposit RM Reports</h4>
        <div className="headTwo pt-4">
          <div className="d-flex justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Select RM</label>
              <select className="form-control" name="branches" onChange={(e) => fetchZonal(e)}>
                <option value="">Select RM</option>
                {RM.map((item, index) => <option key={index + item.employee_id} value={JSON.stringify(item)}>{item.role} ({item.employee_id})</option>)}
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">From Date</label>
              <DatePicker className="form-control" selected={startDate} onChange={date => dateChange(date)} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">To Date</label>
              <DatePicker className="form-control" selected={endDate} onChange={date => endDateChange(date)} />
            </div>
          </div>
          <div className="d-flex" style={{ width: "100%" }}>
            <button
              type="submit"
              className="btn btn-primary d-block btn-sm mx-auto"
              style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
              onClick={() => submit()}
            >
              Submit
            </button>
          </div>
        </div>
      </section>

      <table className="table table-bordered">
        <thead>
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Customer Name</th>
            <th scope="col">Company Name</th>
            <th scope="col">Schema Name</th>
            <th scope="col">Premium</th>
            <th scope="col">Mode of Payment</th>
            <th scope="col">Employee ID</th>
          </tr>
        </thead>
        <tbody>
          {
            (data !== undefined) &&
            data.map((item, index) => {
              return (
                <tr key={index}>
                  <th scope="row">{index + 1}</th>
                  <td><small>{item.firstname} {item.lastname}</small></td>
                  <td><small>{item.company}</small></td>
                  <td><small>{item.schema}</small></td>
                  <td><small>{item.amount}</small></td>
                  <td><small>{item.payment_mode}</small></td>
                  <td><small>{item.manager_id}</small></td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Fragment>
  );
}

export default withRouter(Example);