import React, { useState, Fragment } from "react";
import { Button, FormGroup, FormControl } from "react-bootstrap";
import "../src/test.css";
import Background from "./image.png";
import Header from "./header";
import Footer from "./footer";
import { withRouter } from "react-router-dom";
import { request } from './helper';

function Login(props) {
  const [UserID, setUserID] = useState("");
  const [password, setPassword] = useState("");

  let validateForm = async () => {
    if (UserID && password) {
      try {
        let loginResponse = await (await request('employee/login').post({ employee_id: UserID, password: password })).json();

        if (loginResponse.status === 200) {
          props.history.push({
            pathname: `/Home`,
            state: loginResponse.message,
          });
        }
      } catch (e) {
        console.log(e);
      }
    }
  }

  function handleSubmit(event) {
    event.preventDefault();
  }

  return (
    <Fragment>
      <section className="image" >
        <div className="">
          <div className="text">
            <h1>CRM for Investment Services</h1>
          </div>
          <div className="login">

            <form className="p-5 Homeform" onSubmit={handleSubmit} style={{ borderRadius: "10px" }}>
              <FormGroup controlId="UserID">
                <FormControl
                  autoFocus
                  placeholder="UserID"
                  type="UserID"
                  value={UserID}
                  onChange={e => setUserID(e.target.value)}
                  style={{ backgroundColor: "white" }}
                />
              </FormGroup>
              <FormGroup controlId="password">
                <FormControl
                  placeholder="password"
                  value={password}
                  onChange={e => setPassword(e.target.value)}
                  type="password"
                />
              </FormGroup>
              <Button className="Button btn btn-primary d-block mx-auto" type="submit" onClick={() => validateForm()} >
                Login
              </Button>
              <div className="password">
                <a href=" "><b>Forget Password?</b></a>
              </div>
            </form>
          </div>
        </div>
      </section>
      <Footer />
    </Fragment>
  );
}
export default withRouter(Login);