import { Router, Route, BrowserRouter } from "react-router-dom";
import './App.css';
import Header from "../src/header";
import Home from "../src/CRMHomeJS";
import Footer from "../src/footer";
import CRMfirst from "../src/CRMFirst";

function App() {
  return (
    <BrowserRouter>
      <Header />
      <Route exact path="/" component={Home} />
      <Route path="/Home" component={CRMfirst} />
    </BrowserRouter>
  );
}

export default App;
