import React, { Fragment } from 'react';
import { request } from '../helper';

export default class FixedDepositsRevenue extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      schema: '',
      company: '',
      tenour: '',
      revenue: '',
      getData: null
    }
  }

  async componentDidMount() {
    let res = await request('fixedinsurancerevenue/select').get();

    this.setState({ getData: await res.json() });
  }

  handleChange = event => {
    if (event.target.name === "tenour") {
      if (isNaN(event.target.value)) {
        alert("Please enter a Valid Number");
        return;
      }
    }

    this.setState({
      [event.target.name]: event.target.value,
    });
  }

  handleSubmit = async (event) => {
    event.preventDefault();

    const data = {
      schema: this.state.schema,
      company: this.state.company,
      tenour: Number(this.state.tenour),
      revenue: Number(this.state.revenue)
    }

    let res = await request("fixedinsurancerevenue/add").post(data);
    let response = await res.json();

    if (response.status === 200) {
      this.setState({ getData: response });
      alert("Revenue added successfully");
    } else if (response.status === 302) {
      alert("Revenue Already Existed");
    }

    Array
      .from(document.querySelectorAll("input"))
      .forEach(input => (input.value = ""));

    this.setState({ itemvalues: [{}] });
  };

  delete = async (id) => {
    if (window.confirm("Are you sure you want to Delete the Revenue ?")) {
      let response = await request(`fixedinsurancerevenue/delete/${id}`).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }

  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-9 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Fixed Deposits Revenue</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Company Name</label>
                  <input type="text" onChange={this.handleChange} name="company" className="form-control" required id="exampleInputEmail1" placeholder="Company Name" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Schema Name</label>
                  <input type="text" onChange={this.handleChange} name="schema" className="form-control" required id="exampleInputPassword1" placeholder="Schema Name" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Tenour</label>
                  <input type="text" onChange={this.handleChange} name="tenour" className="form-control" required id="exampleInputEmail1" placeholder="Tenour" autoComplete="off" />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Revenue</label>
                  <input type="text" onChange={this.handleChange} name="revenue" className="form-control" required id="exampleInputPassword1" placeholder="Revenue" autoComplete="off" />
                </div>
              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}>Add</button>
              </div>
            </form>
          </div>
        </section>

        <h5 className="mt-5">Fixed Deposits Revenue Details</h5>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">Company Name</th>
              <th scope="col">Schema Name</th>
              <th scope="col">Tenour</th>
              <th scope="col">Revenue</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>

            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.company}</small></td>
                      <td><small>{dataa.schema}</small></td>
                      <td><small>{dataa.tenour}</small></td>
                      <td><small>{dataa.revenue}%</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }
          </tbody>
        </table>
      </Fragment>
    );
  }
}