import React, { Fragment } from 'react';
import { request } from '../helper';

export default class MutualFundsRevenue extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      amc: '',
      funds: '',
      plan: '',
      option: '',
      mode: '',
      revenue: '',
      sub_option: null,
      getData: null
    }
  }

  async componentDidMount() {
    let res = await request('mutualfundsrevenue/select').get();
    this.setState({
      getData: await res.json()
    });
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      amc: this.state.amc,
      funds: this.state.funds,
      plan: this.state.plan,
      option: this.state.option,
      mode: this.state.mode,
      revenue: this.state.revenue,
      sub_option: this.state.sub_option,
    }

    let res = await request("mutualfundsrevenue/add").post(data);
    let response = await res.json();

    if (response.status === 200) {
      this.setState({ getData: response });
      alert("Revenue added successfully");
    } else if (response.status === 302) {
      alert("Revenue Already Existed");
    }

    Array
      .from(document.querySelectorAll("input"))
      .forEach(input => (input.value = ""));

    this.setState({ itemvalues: [{}] });
  };

  delete = async (id) => {
    if (window.confirm("Delete the Revenue ?")) {
      let response = await request(`mutualfundsrevenue/delete/${id}`).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }

  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Mutual Funds Revenue</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit} >
              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">AMC Name</label>
                  <input autoComplete="off"
                    type="text"
                    className="form-control"
                    onChange={this.handleChange}
                    name="amc"
                    id="exampleInputEmail1"
                    aria-describedby="emailHelp"
                    placeholder="AMC Name"
                  />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Funds Name</label>
                  <input autoComplete="off"
                    type="text"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Funds"
                    onChange={this.handleChange}
                    name="funds"
                  />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Plan Name</label>
                  <input autoComplete="off"
                    type="text"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Plan"
                    onChange={this.handleChange}
                    name="plan"
                  />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Option</label>
                  <select className="form-control" name="option" id="empProductName" onChange={this.handleChange}>
                    <option value="">Select MOP</option>
                    <option value="Growth">Growth</option>
                    <option value="Dividend">Dividend</option>
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Sub Option</label>
                  <input autoComplete="off"
                    type="text"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Sub Option"
                    onChange={this.handleChange}
                    name="sub_option"
                  />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Revenue</label>
                  <input autoComplete="off"
                    type="text"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Revenue"
                    onChange={this.handleChange}
                    name="revenue"
                    value={this.state.revenue}
                  />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Mode</label>
                  <select className="form-control" name="mode" id="empProductName" onChange={this.handleChange}>
                    <option value="">Select Mode</option>
                    <option value="SWP">SWP</option>
                    <option value="SIP">SIP</option>
                    <option value="LUMPSUM">LUMPSUM</option>
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Tenour</label>
                  <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Tenour" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                <input type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }} />
              </div>
            </form>
          </div>

        </section>

        <h5 className="mt-5">Mutual Funds Revenue Details</h5>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">AMC Name</th>
              <th scope="col">Funds</th>
              <th scope="col">Plan</th>
              <th scope="col">Mode</th>
              <th scope="col">Option</th>
              <th scope="col">Sub Option</th>
              <th scope="col">Revenue</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>
            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.amc}</small></td>
                      <td><small>{dataa.funds}</small></td>
                      <td><small>{dataa.plan}</small></td>
                      <td><small>{dataa.mode}</small></td>
                      <td><small>{dataa.option}</small></td>
                      <td><small>{dataa.sub_option}</small></td>
                      <td><small>{dataa.revenue}</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }
          </tbody>
        </table>
      </Fragment>
    );
  }
}