import React, { Fragment, useState } from 'react';
import { withRouter } from 'react-router-dom';
import DatePicker from "react-datepicker";

const Example = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [region, setRegion] = useState('');

  let selectedDate = (date, func) => func(date);

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Life Insurance Regional Reports</h4>
        <div className="headTwo pt-4">
          <div className="d-flex justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Select Region</label>
              <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Select Region" onChange={(e) => setRegion(e.target.value)} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">From Date</label>
              <DatePicker className="form-control" selected={startDate} onChange={date => selectedDate(date, setStartDate)} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">To Date</label>
              <DatePicker className="form-control" selected={endDate} onChange={date => selectedDate(date, setEndDate)} />
            </div>
          </div>
          <div className="d-flex" style={{ width: "100%" }}>
            <button
              type="submit"
              className="btn btn-primary d-block btn-sm mx-auto"
              style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
              onClick={() => props.history.push("/Home/Transaction_Reports")}
            >
              Submit
            </button>
          </div>
        </div>
      </section>
    </Fragment>
  );
}

export default withRouter(Example);