import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';

class MutualFunds extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      data: [],
    }
  }

  componentDidMount() {
    this.setState({
      data: this.props.location.state,
    }, () => console.log(this.state.data));
  }

  render() {
    return (
      <Fragment>
        <section className="mx-auto col-sm-7 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2">Reports</h4>
          <div className="headTwo pt-4">
            <div className="form-group pt-0 pb-0 pr-3 pl-3" >
              <label htmlFor="exampleInputEmail1" className="mb-0">Employee ID </label>
              <input type="text" className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Employee ID" />
            </div>
            <div className="d-flex" style={{ width: "100%" }}>
              <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
            </div>
          </div>
        </section>

        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">Customer Name</th>
              <th scope="col">Comapny Name</th>
              <th scope="col">Plan Name</th>
              <th scope="col">Premium</th>
              <th scope="col">Mode of payment</th>
              <th scope="col">Policy term</th>
              <th scope="col">Employee ID</th>
              <th scope="col">Revenue</th>
              <th scope="col">Branch</th>
            </tr>
          </thead>
          <tbody>
            {
              (this.state.data !== undefined) &&
              this.state.data.map((item, index) => {
                return (
                  <tr key={index}>
                    <th scope="row">{index + 1}</th>
                    <td><small>{item.firstname} {item.lastname}</small></td>
                    <td><small>{item.company || item.amc}</small></td>
                    <td><small>{item.plan_name || item.plan}</small></td>
                    <td><small>{item.net_premium || item.amount}</small></td>
                    <td><small>{item.payment_mode}</small></td>
                    <td><small>{item.policy_term || item.option}</small></td>
                    <td><small>{item.manager_id}</small></td>
                    <td><small>{item.revenue}</small></td>
                    <td><small>{item.branch}</small></td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>
      </Fragment>
    );
  }
}

export default withRouter(MutualFunds);