import React, { Fragment } from 'react';
import { request } from '../helper';

export default class LifeInsuranceRevenue extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      product: '',
      company: '',
      plan: '',
      premium_payment_term: '',
      revenue: '',
      getData: null
    }
  }

  async componentDidMount() {
    let res = await request('lifeinsurancerevenue/select').get();
    this.setState({ getData: await res.json() });
  }

  handleChange = event => {
    if (event.target.name === "revenue") {
      if (isNaN(event.target.value)) {
        alert("Please enter valid Number");
        return;
      } else {
        this.setState({ [event.target.name]: event.target.value });
      }
    } else {
      this.setState({ [event.target.name]: event.target.value });
    }
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      product: this.state.product,
      company: this.state.company,
      plan: this.state.plan,
      premium_payment_term: this.state.premium_payment_term,
      revenue: this.state.revenue
    }

    let res = await request("lifeinsurancerevenue/add").post(data);
    let response = await res.json();

    if (response.status === 200) {
      this.setState({ getData: response });
      alert("Revenue added successfully");
    } else if (response.status === 302) {
      alert("Revenue Already Existed");
    }

    Array
      .from(document.querySelectorAll("input"))
      .forEach(input => (input.value = ""), select => (select.value = ""));

    this.setState({ itemvalues: [{}] });
  };

  delete = async (id) => {
    if (window.confirm("Delete the Revenue ?")) {
      let response = await request(`lifeinsurancerevenue/delete/${id}`).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }

  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Life Insurance Revenue</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit} >
              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Product Name</label>
                  <input type="text" className="form-control" name="product" required onChange={this.handleChange} placeholder="Product Name" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Company Name</label>
                  <input type="text" className="form-control" name="company" required onChange={this.handleChange} placeholder="Company Name" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Plan Name</label>
                  <input type="text" className="form-control" name="plan" required onChange={this.handleChange} placeholder="Plan Name" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Premium Payment Term</label>
                  <input type="text" className="form-control" name="premium_payment_term" required onChange={this.handleChange} placeholder="Premium Payment Term" autoComplete="off" />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Revenue</label>
                  <input type="text" className="form-control" name="revenue" required onChange={this.handleChange} placeholder="Revenue" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex" style={{ width: "100%" }}>
                <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
              </div>
            </form>
          </div>

        </section>

        <h5 className="mt-5">Life Insurance Revenue Details</h5>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">Company Name</th>
              <th scope="col">Product Name</th>
              <th scope="col">Plan Name</th>
              <th scope="col">Premium Payment Term</th>
              <th scope="col">Revenue</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>
            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.company}</small></td>
                      <td><small>{dataa.product}</small></td>
                      <td><small>{dataa.plan}</small></td>
                      <td><small>{dataa.premium_payment_term}</small></td>
                      <td><small>{dataa.revenue}%</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }

          </tbody>
        </table>
      </Fragment>
    );
  }
}