import React, { Fragment } from 'react';
import { request } from '../helper';

export default class GeneralInsuranceRevenue extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      transactionType: 'Travel',
      product: '',
      company: '',
      revenue: '',
      mode: '',
      getData: null
    }
  }

  async componentDidMount() {
    let res = await request('generalinsurancerevenue/select').get();
    this.setState({
      getData: await res.json()
    });
  }

  handleChange = event => {
    if (event.target.name === "revenue") {
      if (isNaN(event.target.value)) {
        alert("Please enter valid Number");
        return;
      } else {
        this.setState({
          [event.target.name]: event.target.value
        });
      }
    } else {
      this.setState({
        [event.target.name]: event.target.value
      });
    }
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      product: this.state.product,
      company: this.state.company,
      type: this.state.transactionType,
      revenue: this.state.revenue,
      mode: this.state.mode,
    }

    let res = await request("generalinsurancerevenue/add").post(data);
    let response = await res.json();

    if (response.status === 200) {
      this.setState({ getData: response });
      alert("Revenue added successfully");
    } else if (response.status === 302) {
      alert("Revenue Already Existed");
    }

    Array
      .from(document.querySelectorAll("input"))
      .forEach(input => (input.value = ""));

    this.setState({ itemvalues: [{}] });
  };

  delete = async (id) => {
    if (window.confirm("Delete the Revenue ?")) {
      let response = await request(`generalinsurancerevenue/delete/${id}`).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }

  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add General Insurance Revenue</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit} >
              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" >
                  <label htmlFor="exampleInputEmail1" className="mb-0">Product Name :</label>
                  <input required autoComplete="off"
                    type="text"
                    className="form-control shadow"
                    id="exampleInputEmail1"
                    aria-describedby="emailHelp"
                    placeholder="Product Name"
                    onChange={this.handleChange}
                    name="product"
                    value={this.state.product}
                  />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Company Name :</label>
                  <input autoComplete="off"
                    type="text" required
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Company Name"
                    onChange={this.handleChange}
                    name="company"
                    value={this.state.company}
                  />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Type of Insurance:</label>
                  <select className="form-control" id="empProductName" name="transactionType" required onChange={this.handleChange}>
                    <option value="">Select Type of Insurance</option>
                    <option value="Health">Health</option>
                    <option value="Travel">Travel</option>
                  </select>
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Mode of Transaction :</label>
                  {
                    (this.state.transactionType === "Health")
                      ? (
                        <select className="form-control" id="empProductName" required name="mode" onChange={this.handleChange}>
                          <option value="">Select Mode of Insurance</option>
                          <option value="Premium">Premium</option>
                        </select>
                      )
                      : (
                        <select className="form-control" id="empProductName" required name="mode" onChange={this.handleChange}>
                          <option value="">Select Mode of Insurance</option>
                          <option value="TP with OD">TP with OD</option>
                          <option value="TP without OD">TP without OD</option>
                          <option value="OD">OD</option>
                          <option value="Comprehensive">Comprehensive</option>
                        </select>
                      )
                  }
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Revenue :</label>
                  <input required autoComplete="off"
                    type="text"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Revenue"
                    onChange={this.handleChange}
                    name="revenue"
                    value={this.state.revenue}
                  />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>

              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
              </div>
            </form>
          </div>
        </section>


        <h5 className="mt-5">General Insurance Revenue Details</h5>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">Company Name</th>
              <th scope="col">Product Name</th>
              <th scope="col">Mode of Transaction</th>
              <th scope="col">Type of Transaction</th>
              <th scope="col">Revenue</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>

            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.company}</small></td>
                      <td><small>{dataa.product}</small></td>
                      <td><small>{dataa.mode}</small></td>
                      <td><small>{dataa.type}</small></td>
                      <td><small>{dataa.revenue}</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }
          </tbody>
        </table>
      </Fragment>
    );
  }
}