import React, { Fragment, useState } from 'react';
import { withRouter } from 'react-router-dom';
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";


const Example = (props) => 
   {
   
   const [startDate, setStartDate] = useState(new Date());
   const [endDate, setEndDate] = useState(new Date());
      return (
         <Fragment>
            <section className="mx-auto col-sm-12 p-3 m-0 rounded">
               <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Mutual Funds Regional Reports</h4>
               <div className="headTwo pt-4">
                  <div className="d-flex justify-content-between mt-3">
                     <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                        <label htmlFor="exampleInputPassword1" className="mb-0">Select Regional</label>
                        <DatePicker  className="form-control" selected={startDate} onChange={date => setStartDate(date)} />
                     </div>
                     <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                        <label htmlFor="exampleInputPassword1" className="mb-0">From Date</label>
                        <input type="password" className="form-control" id="exampleInputPassword1" placeholder="From Date" />
                     </div>
                     <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                        <label htmlFor="exampleInputPassword1" className="mb-0">To Date</label>
                        <DatePicker  className="form-control" selected={endDate} onChange={date => setEndDate(date)} />
                     </div>
                  </div> 
                  <div className="d-flex" style={{ width: "100%" }}>
                        <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style = {{borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white",border: "none"}}
                        onClick={() => props.history.push("/Home/Transaction_Reports")}>Submit</button>
                  </div>
               </div>  
            </section>
         </Fragment>
      );
   }


export default withRouter(Example);