import React, { Fragment, useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { request } from '../helper';
import { withRouter } from 'react-router-dom';

const Example = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  const [policyDate, setPolicyDate] = useState(new Date());
  const [chequeDate, setChequeDate] = useState(new Date());
  const [dateOfEntry, setDateOfEntry] = useState(new Date());
  const [maturityDate, setMaturityDate] = useState(new Date());
  const [data, setData] = useState('');
  const [companies, setCompanies] = useState([]);
  const [schema, setSchema] = useState([]);
  const [tenour, setTenour] = useState([]);
  const [comp, setComp] = useState('');
  const [mop, setMOP] = useState('');
  const [selectedTenour, setSelectedTenour] = useState('');
  const [revenue, setRevenue] = useState(0);
  const [amount, setAmount] = useState('');
  const [mode, setMode] = useState('');
  const [state, setState] = useState({});
  const [selectedProd, setSelectedProd] = useState('');
  const [selectedSchema, setSelectedSchema] = useState('');
  const [selectedMaturityDate, setSelectedMaturityDate] = useState('');
  const [dateOfBirth, setDateOfBirth] = useState(new Date());

  const Mobile = React.useRef(null);
  const Aadhaar = React.useRef(null);
  const Branch = React.useRef(null);

  useEffect(() => {
    if (props.location.state === undefined) {
      console.log(props.location.state);
      props.history.push('/Home/generalinsurancetransaction');
      return;
    }

    setData(props.location.state);
    (async () => {
      let res = await request('fixedinsurancerevenue/columns').get();
      setCompanies(await res.json());
    })();
  }, []);

  let dateFormat = (date) => {
    let [month, day, year] = date.split("/");

    if (month > Number(12)) {
      return `${month}-${day}-${year}`
    }

    return `${day}-${month}-${year}`;
  }

  let fetchPlans = async ({ target: { value } }) => {
    setComp(value);
    let res = await request(`fixedinsurancerevenue/schema/${value}`).get();
    setSchema(await res.json());
  }

  let fetchTenour = async ({ target: { value } }) => {
    setSelectedSchema(value);
    let res = await request(`fixedinsurancerevenue/tenour/${comp}/${value}`).get();
    setTenour(await res.json());
  }

  let entryDate = (e) => setDateOfEntry(e);

  let dateOfPolicy = (e) => setPolicyDate(e);

  let dateOfCheck = (e) => setChequeDate(e);

  let birthDate = (e) => setDateOfBirth(e);

  let dateOfMaturity = (e) => {
    setMaturityDate(e);
    setSelectedMaturityDate(
      // dateFormat(
      new Date(e).toLocaleDateString()
      // )
    );
  };

  let check = () => {
    if (schema.length === 0) {
      alert("Please Select a Company");
      return false;
    } else if (tenour.length === 0) {
      alert("Please Select a Schema");
      return false;
    }

    return true;
  }

  let changeHandler = ({ target: { value, name } }) => {
    let obj = state;

    obj[name] = value;

    setState(obj);

    console.log(state);
  }

  let fetchRevenue = (amount, tenor) => {

    if (!check()) return;

    if (isNaN(amount)) {
      alert("Please select a valid Number");
      return;
    }

    var tenr = JSON.parse(selectedTenour);

    setAmount(amount);
    setRevenue(Number(amount) / 100 * Number(tenr["revenue"]));

    console.log(Number(amount));
    console.log(amount);
  }

  let func = () => {
    switch (mode) {
      case "Cheque":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" name="cheque_number" id="exampleInputPassword1" placeholder="Cheque Number" onChange={(e) => changeHandler(e)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" name="cheque_account" id="exampleInputPassword1" placeholder="Cheque Account" onChange={(e) => changeHandler(e)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDate} onChange={date => dateOfCheck(date)} />
                </div>
              </div>
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" name="bank_name" id="exampleInputPassword1" placeholder="Bank Name" onChange={(e) => changeHandler(e)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                </div>

              </div>
            </div>
          </Fragment>
        );
      case "DD":
        return (
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" name="account_number" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Account Number" onChange={(e) => changeHandler(e)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Number</label>
                  <input type="text" className="form-control" name="cheque_number" id="exampleInputPassword1" placeholder="Cheque Number" onChange={(e) => changeHandler(e)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Cheque Account</label>
                  <input type="text" className="form-control" name="cheque_account" id="exampleInputPassword1" placeholder="Cheque Account" onChange={(e) => changeHandler(e)} />
                </div>
              </div>

              <div className="row">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Cheque Date</label>
                  <DatePicker className="form-control" selected={chequeDate} onChange={date => dateOfCheck(date)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Bank Name</label>
                  <input type="text" className="form-control" name="bank_name" id="exampleInputPassword1" placeholder="Bank Name" onChange={(e) => changeHandler(e)} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>
          </Fragment>
        );
      case "Online":
        return (
          <Fragment>
            <div className="container">
              <div className="row">

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Account Number</label>
                  <input type="text" className="form-control" name="account_number" onChange={(e) => changeHandler(e)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Account Number" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Reference Number</label>
                  <input type="text" className="form-control" name="reference_number" onChange={(e) => changeHandler(e)} id="exampleInputPassword1" placeholder="Reference Name" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
              </div>
            </div>
          </Fragment>
        );
      default:
        return null;
    }
  }

  let submit = async () => {
    var tenr = JSON.parse(selectedTenour);
    var req = {
      ...state,
      company: comp,
      product: selectedProd,
      schema: selectedSchema,
      tenour: `${tenr["tenour"]} Years`,
      maturity_date: selectedMaturityDate,
      branch: Branch.current.value,
      mobile: Number(Mobile.current.value),
      aadhaar: Number(Aadhaar.current.value)
    }

    let res = await request('fixeddepositstransactions/add').post(req);
    let json = await res.text();
    if (res.status === 200) {
      alert("Transaction Added Successfully");
      props.history.push({ pathname: `/Home` });
    }
    else if (res.status === 302) {
      alert("Transaction Already Existed");
      props.history.push({ pathname: `/Home` });
    }
    console.log(json);
    console.log(req);
  }

  return (
    <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Fixed Deposit Transaction</h4>
        <div className="headTwo pt-4">
          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Title</label>
              <input
                type="email"
                className="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                readOnly
                placeholder={data['title']}
                defaultValue={data['title']}
              />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" style={{ display: "block" }}>
              <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
              <input
                type="text"
                className="form-control"
                id="exampleInputPassword1"
                readOnly
                placeholder={data['firstname']}
                defaultValue={data['firstname']}
              />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
              <input
                type="text"
                className="form-control"
                id="exampleInputPassword1"
                readOnly
                placeholder={data['lastname']}
                defaultValue={data['lastname']}
              />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Mobile Number</label>
              <input type="email" className="form-control" id="exampleInputEmail1" ref={Mobile} aria-describedby="emailHelp" readOnly placeholder={data['mobile']} defaultValue={data['mobile']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['email']} defaultValue={data['email']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
              <DatePicker className="form-control" selected={dateOfBirth} onChange={date => birthDate(date)} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Pan Card</label>
              <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['pan']} defaultValue={data['pan']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Aadhaar Number</label>
              <input type="text" className="form-control" id="exampleInputPassword1" ref={Aadhaar} readOnly placeholder={data['aadhaar']} defaultValue={data['aadhaar']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Location</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['location']} defaultValue={data['location']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Branch Name</label>
              <input type="text" className="form-control" id="exampleInputEmail1" ref={Branch} aria-describedby="emailHelp" readOnly placeholder={data['branch']} defaultValue={data['branch']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"> </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          </div>

          <div className="d-flex justify-content-center">
            <h5>Present Address</h5>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
              <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['present_line1']} defaultValue={data['present_line1']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['present_line2']} defaultValue={data['present_line2']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['present_city']} defaultValue={data['present_city']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
              <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['present_district']} defaultValue={data['present_district']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
              <input type="text" className="form-control" id="exampleInputPassword1" readOnly placeholder={data['present_state']} defaultValue={data['present_state']} />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
              <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" readOnly placeholder={data['present_pincode']} defaultValue={data['present_pincode']} />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          </div>

          <div className="d-flex justify-content-center">
            <h5>Business Entry Details</h5>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Select RM Details</label>
              <select className="form-control" name="manager_id" onChange={(e) => changeHandler(e)}>
                <option value="">select RMID</option>
                <option value="50018">50018 (Umamaheswar Rao)</option>
                <option value="50001">50001 (Manohar Reddy)</option>
                <option value="50015">50015 (Radha Krishna)</option>
                <option value="50017">50017 (Venkatesham)</option>
                <option value="50010">50010 (Navya)</option>
                <option value="50016">50016 (Harish)</option>
                <option value="50020">50020 (Reena)</option>
                <option value="50019">50019 (Prashanthi)</option>
                <option value="50021">50021 (Mahesh)</option>
                <option value="50022">50022 (Sana)</option>
                <option value="50027">50027 (Naresh)</option>
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">TSO Details</label>
              <select className="form-control" name="tso_id" onChange={(e) => changeHandler(e)}>
                <option value="">select TSOID</option>
                <option value="50020">50020 (Reena)</option>
                <option value="50019">50019 (Prashanthi)</option>
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Application Number</label>
              <input type="text" className="form-control" name="application_number" onChange={(e) => changeHandler(e)} id="exampleInputPassword1" placeholder="Application Number" />
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Company Name</label>
              <select className="form-control" name="type_of_entry" onChange={(e) => fetchPlans(e)}>
                <option>Company</option>
                {
                  companies.map((company, index) => {
                    return <option key={index + company} value={company}>{company}</option>
                  })
                }
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Product Name</label>
              <select className="form-control" name="type_of_entry" onChange={(e) => setSelectedProd(e.target.value)}>
                <option>Product Name</option>
                <option>Fixed Deposits / NCDs/ NPs</option>
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Schema</label>
              <select className="form-control" name="type_of_entry" onChange={(e) => fetchTenour(e)}>
                <option>Schema</option>
                {
                  schema.map((sche, index) => {
                    return <option key={index + sche} value={sche}>{sche}</option>
                  })
                }
              </select>
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Tenour</label>
              <select className="form-control" name="type_of_entry" onChange={(e) => { setSelectedTenour(e.target.value) }}>
                <option>Tenour</option>
                {
                  tenour.map(({ tenour, revenue }, index) => {
                    return <option key={index + tenour} value={JSON.stringify({ tenour, revenue })}>{tenour} Years</option>
                  })
                }
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Maturity Date</label>
              <DatePicker className="form-control" selected={maturityDate} onChange={date => dateOfMaturity(date)} />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Business Mode</label>
              <select className="form-control" name="mode_of_payment" aria-required="true" aria-invalid="false">
                <option value="">Select Business Type</option>
                <option value="New Business">New Business</option>
                <option value="Renewel">Renewal</option>
              </select>
            </div>
          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Mode Of Payment</label>
              <select className="form-control" name="payment_mode" id="mop" aria-required="true" aria-invalid="false" onChange={(e) => setMode(e.target.value)} >
                <option value="">Select MOP</option>
                <option value="Cheque">Cheque</option>
                <option value="DD">DD</option>
                <option value="Online">Online</option>
                <option value="Cash">Cash</option>
              </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Type Of Business</label>
              <select className="form-control" name="business_mode" id="empProductName" onChange={(e) => changeHandler(e)}>
                <option value="">Select Business</option>
                <option value="Fresh">New business</option>
                <option value="Renewal">Renewel</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Amount</label>
              <input
                type="text"
                className="form-control"
                id="exampleInputPassword1"
                value={amount}
                placeholder="Amount"
                onChange={(e) => fetchRevenue(e.target.value, selectedTenour)}
              />
            </div>

          </div>

          <div className="d-flex justify-content-between">
            {func()}
          </div>

          <div className="d-flex justify-content-between">

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">FD Number</label>
              <input type="email" className="form-control" name="fd_number" id="exampleInputEmail1" onChange={(e) => changeHandler(e)} aria-describedby="emailHelp" placeholder="FD NUmber" />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Policy Issue Date</label>
              <DatePicker className="form-control" selected={policyDate} onChange={date => dateOfPolicy(date)} />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">Date of Entry</label>
              <DatePicker className="form-control" selected={dateOfEntry} onChange={date => entryDate(date)} />
            </div>

          </div>

          <div className="d-flex justify-content-between">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Stage</label>
              <select className="form-control" name="stage" onChange={(e) => changeHandler(e)}>
                <option value="">Select Stage</option>
                <option value="Monthly">Policy Issued</option>
                <option value="Quarterly">Policy Cancelled</option>
                <option value="Half Yearly">Policy Underride</option>
              </select>
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Revenue</label>
              <input
                type="email"
                className="form-control"
                id="exampleInputEmail1"
                readOnly
                value={revenue}
                aria-describedby="emailHelp"
                placeholder="Revenue"
              />
            </div>

            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            </div>
          </div>

          <div className="d-flex" style={{ width: "100%" }}>
            <button
              type="submit"
              onClick={() => submit()}
              className="btn btn-primary d-block btn-sm mx-auto"
              style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
            >
              Submit
            </button>
          </div>
        </div>
      </section>
    </Fragment>
  );
}

export default withRouter(Example)