import React, { Fragment } from 'react';
import { request } from '../helper';
import '../css/productUpdation.css'

export default class MutualFunds extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      product: '',
      company: '',
      type: 'Fixed Deposits',
      getData: null
    }
  }

  async componentDidMount() {
    let res = await request('product/select/Fixed Deposits').get();
    this.setState({ getData: await res.json() });
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      product: this.state.product,
      company: this.state.company,
      type: 'Fixed Deposits'
    }

    let res = await request("product/add").post(data);
    let response = await res.json();

    if (response.status === 200) {
      this.setState({ getData: response });
      alert("Product added successfully");
    } else if (response.status === 302) {
      alert("Product Already Existed");
    }

    Array
      .from(document.querySelectorAll("input"))
      .forEach(input => (input.value = ""));

    this.setState({ itemvalues: [{}] });
  };

  delete = async (id) => {
    if (window.confirm("Are you sure you  want to Delete the Product ?")) {
      let response = await request(`product/delete/Fixed Deposits/${id}`).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }

  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-7 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2">Add Fixed Deposit Product</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                <label htmlFor="exampleInputEmail1" className="mb-0">Company Name :</label>
                <input
                  type="text"
                  required
                  onChange={this.handleChange}
                  name="company"
                  className="form-control shadow"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Company Name"
                  autoComplete="off"
                />
              </div>
              <div className="form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Product Name :</label>
                <select className="form-control" name="product" onChange={this.handleChange}>
                  <option value="">Select Product</option>
                  <option>Fixed Deposit</option>
                </select>
              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                <input
                  type="submit"
                  className="btn d-block btn-sm mx-auto"
                  style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }}
                />
              </div>
            </form>
          </div>
        </section>

        <div className="mt-5">
          {/* <input type="text" placeholder="search" className="form-control search" /> */}
          <h5>Fixed Deposit Product Details</h5>
        </div>

        <table className="table table-striped table-responsive-sm table-hover">
          <thead>
            <tr style={{ color: "white" }}>
              <th scope="col">S.No</th>
              <th scope="col">Company Name</th>
              <th scope="col">Product Name</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>

          <tbody>
            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.company}</small></td>
                      <td><small>{dataa.product}</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }
          </tbody>
        </table>
      </Fragment>
    );
  }
}