import React, { Fragment } from 'react';
import { request } from '../helper';

export default class MutualFunds extends React.Component {
  constructor (props) {
    super(props)
    this.state = { branch: '', location: '', getData: null }
  }

  async componentDidMount() {
    let res = await request('branch/all').get();
    console.log(res);
    this.setState({
      getData: await res.json()

    });

  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      location: this.state.location,
      branch: this.state.branch,
    }

    let res = await request("branch/add").post(data);

    let response = await res.json();

    if (response.status === 200) {
      this.setState({
        getData: response
      });
      alert("Branch added successfully");
    }
    else if (response.status === 302) {
      alert("Branch Already Existed");
    }
    Array.from(document.querySelectorAll("input")).forEach(
      input => (input.value = "")
    );
    this.setState({
      itemvalues: [{}]
    });
  };

  delete = async (id) => {
    if (window.confirm("Delete the Branch ?")) {
      let response = await request(`branch/delete/${id}`).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }
  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-7 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2">Add Loaction and Branch</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                <label htmlFor="exampleInputEmail1" className="mb-0">Add Location </label>
                <input type="text" onChange={this.handleChange} name="location" required className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Location" autoComplete="off" />
              </div>
              <div className="form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Add Branch </label>
                <input type="text" onChange={this.handleChange} name="branch" required className="form-control" id="exampleInputPassword1" placeholder="Branch" autoComplete="off" />
              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
              </div>
            </form>
          </div>
        </section>

        <h5 className="mt-5">Loaction and Branch Details</h5>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">Location</th>
              <th scope="col">Branch</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>
            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.location}</small></td>
                      <td><small>{dataa.branch}</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }


          </tbody>
        </table>
      </Fragment>
    );
  }
}